<template>
  <div class="hello">

    <!-- 在此插入Navbar；並用 props讓Navbar元件接index的資料 -->
    <Index_Navbar :shoppingList='cartList'></Index_Navbar>



    <!-- 痾樂 -->
    <Alert />

    <!-- Jumbo大圖 -->
    <div class="header jumbo-bg bg-cover d-flex align-items-end mb-3">
      <div class="pb-6 px-3">
        <div class="bg-lighter p-5">
          <p class="h1">我有酒，你有故事嗎?</p>
          <p class="h3">Do you have story?</p>
          <p class="h3">If you don't, I would ask you again later.</p>
        </div>
      </div>
    </div>


    <div class="row">


      <!-- 產品服務頁面；左邊導覽欄位 -->
      <div class="col-md-2 mb-3">

        <div class="list-group sticky-top" id="list-tab" role="tablist">

          <router-link class="list-group-item list-group-item-action active" data-toggle="list" to="/services"
            role="tab">Whiskey
          </router-link>

          <router-link class="list-group-item list-group-item-action" data-toggle="list" to="/services/travel"
            role="tab">Travel
          </router-link>

          <router-link class="list-group-item list-group-item-action" data-toggle="list" to="/services/party"
            role="tab">Party
          </router-link>

          <router-link class="list-group-item list-group-item-action" data-toggle="list" to="/services/social"
            role="tab">Social Area
          </router-link>

        </div>
      </div>



      <!-- 產品服務、跳轉、顯示位置 -->

      <div class="col-md-10 main">
        <router-view></router-view>
      </div>


    </div>
    <Index_Footer />


  </div>

</template>

<script>
  import Index_Navbar from '../Index_Navbar';
  import Index_Footer from '../Index_Footer';

  import Alert from '../AlertMessage'; //痾樂

  export default {
    components: {
      Index_Navbar,
      Index_Footer,
      Alert,
    },

    // 以下 data、methods、created為了 Navbar的購物車所準備。
    data() {
      return {
        cartList: {
          carts: {}
        },

      }
    },
    methods: {
      getCart() {
        const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/cart`;
        const vm = this;

        this.$http.get(api).then((response) => {
          vm.cartList = response.data.data;
          console.log('購物車清單', response.data.data);
        })
      },
    },

    created(){
      this.getCart();
    }


  }

</script>

<style scoped>
  .bg-cover {
    background-size: cover;
    background-position: center center
  }

  .btn-cart .badge {
    position: absolute;
  }


  .jumbo-bg {
    background-image: url('../../assets/image/products_jumbo.jpg');
    height: 496px;
  }

  .bg-lighter {
    background-color: rgb(255, 255, 255, 0.3)
  }
</style>


