<template>
  <div>
    <nav class="navbar navbar-expand-md navbar-light bg-light">
      <div class="container">

        <button class="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent">
          <span class="navbar-toggler-icon"></span>
        </button>

        <router-link class="navbar-brand shadow-none  d-flex align-items-center bg-transparent" to="/index">

          <img src="../assets/image/logo_1.jpg" height="50" class="d-inline-block align-top" alt="logo">
          <span class="h3 px-2 mb-0 text-barMain font_2">Jared's Whiskey Cabinet</span>

        </router-link>

        <div class="dropdown order-md-1">
          <button style="cursor:pointer" class="btn btn-cart text-barMain" type="button" id="dropdownMenuButton"
            data-toggle="dropdown">
            <i class="fas fa-wine-bottle fa-2x abs"></i>
            <span class="badge badge-danger">{{shoppingList.carts.length}}</span>
          </button>
          <div class="dropdown-menu dropdown-menu-right" style="min-width:550px" aria-labelledby="dropdownMenuButton">
            <table class="table">
              <thead>
                <tr>
                  <th scope="row">*</th>
                  <th>#</th>
                  <th>品名</th>
                  <th>數量</th>
                  <th>小計</th>
                </tr>
              </thead>
              <tbody>
                <tr v-for="(item,index) in shoppingList.carts">
                  <td scope="row"><i class="fas fa-trash-alt"></i></td>
                  <td>{{index+1}}</td>
                  <td class="font-weight-bold">{{item.product.title}}</td>
                  <td>{{item.qty}}/{{item.product.unit}}</td>
                  <td class="text-right">{{item.final_total | currency}}</td>
                </tr>
              </tbody>
              <tfoot>
                <tr class="text-barMain h5 font-weight-bold">
                  <td colspan="3"></td>
                  <td>總金額</td>
                  <td class="text-right">{{shoppingList.final_total | currency}}</td>
                </tr>
              </tfoot>
            </table>
            <router-link class="btn btn-primary btn-block" to="/shoppingCart">
              結帳去
            </router-link>
          </div>
        </div>

        <div class="collapse navbar-collapse" id="navbarSupportedContent">
          <ul class="navbar-nav ml-auto">
            <li class="nav-item active">
              <router-link class="h5 nav-link nav_text" to="/index">首頁 <span class="sr-only">(current)</span>
              </router-link>
            </li>

            <li class="nav-item">
              <router-link class="h5 nav-link nav_text" to="/services">威士忌</router-link>
            </li>

            <li class="nav-item">
              <router-link class="h5 nav-link nav_text" to="/services/travel">旅遊</router-link>
            </li>

            <li class="nav-item">
              <router-link class="h5 nav-link nav_text" to="/services/party">派對</router-link>
            </li>

            <li class="nav-item">
              <router-link class="h5 nav-link nav_text" to="/admin/products">登入</router-link>
            </li>
          </ul>
        </div>


      </div>
    </nav>
  </div>
</template>



<script>
  export default {
    name: 'IndexNavbar',
    props: ["shoppingList"], // 使用props接收index內的資料。

    data() {
      return {

      }

    },
  }

</script>

<style lang="scss">
  @import "../assets/helpers/index_style.scss";
</style>


